<template>
	<div class="dataShow">
		<div id="top">
			<div class="flex">
				<div class="flex flex-direction align-items-center">
					<img src="../assets/img/app_btn_back.png" style="height: 18px;margin:0 10px 0 10px">
				</div>
				<el-input placeholder="请输入内容" prefix-icon="el-icon-search" v-model="searchText" style="width: 100%;margin-right: 30px;">
				</el-input>
			</div>
		</div>
		<div class="swiper-container" id="page">
			<div class="swiper-wrapper">
				<!-- 我的文件-->
				<div class="swiper-slide slidepage">
					{{allFieldType}}
					<div style="background: #fff;">
						<el-row :gutter="20" v-for="(item, index) in fileList||[]" :key="index" style="height: 1rem;">
							<el-col :span="6">
								{{item.icon}}{{index}}
							</el-col>
							<el-col :span="18">
								{{item.name}}
							</el-col>
						</el-row>
					</div>
				</div>
				<!-- 我的审批-->
				<div class="swiper-slide slidepage">
					<div style="text-align: left;margin-left: 0.1rem;position: absolute;">
						<el-button type="text" @click="type=null;getApproveRecordList();">全部</el-button>
						<el-button type="text" @click="type='1';getApproveRecordList();">待审批单据</el-button>
						<el-button type="text" @click="type='2';getApproveRecordList();">已审批单据</el-button>
					</div>
					<div style="background: #fff;margin-top: 1rem;">
						<el-row :gutter="20" v-for="(item, index) in boxTableData||[]" :key="index" style="height: 1rem;line-height: 1rem;border-bottom: 1px solid red;">
							<el-col :span="6">
								<el-radio v-model="item.checkVal"></el-radio>
								{{item.documentName}}
							</el-col>
							<el-col :span="18">
								{{item.documentName}}--{{item.applyName}}
								{{item.createTime}}--{{item.documentType}}
							</el-col>
						</el-row>
					</div>
				</div>
				<!-- 我的消息提醒 -->
				<div class="swiper-slide slidepage">
					<div style="background: #fff;">
						<el-row :gutter="20" v-for="(item, index) in fileList||[]" :key="index" style="height: 1rem;">
							<el-col :span="6">
								{{item.icon}}{{index}}
							</el-col>
							<el-col :span="18">
								{{item.name}}
							</el-col>
						</el-row>
					</div>
				</div>
			</div>
		</div>
		<div class="bottomClass">
			<div class="swiper-container" id="nav">
				<div class="swiper-wrapper">
					<div class="swiper-slide">
						<img src="../assets/img/file.png" style="width: 0.8rem;height: 0.8rem;margin-right: 10px">
						<span>我的文件</span>
					</div>
					<div class="swiper-slide">
						<img src="../assets/img/shenpi.png" style="width: 0.8rem;height: 0.8rem;margin-right: 10px">
						<span>我的审批</span>
					</div>
					<div class="swiper-slide">
						<img src="../assets/img/msg.png" style="width: 0.8rem;height: 0.8rem;margin-right: 10px">
						<span>我的消息提醒</span>
					</div>
					<div class="bar">
						<div class="color"></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import {
		swiperFun
	} from './swiper.js'
	import API from '@/assets/js/api.js';
	export default {
		props: {},
		data() {
			return {
				searchText: null,
				fileList: [{
					name: 23
				}],
				boxTableData: [],
				pn: 1,
				ps: 20,
				type: '1',
				searchText: '',
				allFieldType: [],
			};
		},
		mounted() {
			swiperFun();
			this.getmsgFun();
			this.getAllFieldType();
		},
		methods: {
			async getAllFieldType() {
				const {
					code,
					data
				} = await API.getAllFieldType();
				if (code == '0000') {
					this.allFieldType = data;
				}
			},
			async getmsgFun() {
				const {
					code,
					data,
				} = await API.remindbasicinfo();
				if (code == '0000') {
				}
			},
			async getApproveRecordList() { //审批
				let obj = {};
				if (this.type) {
					obj = {
						"approveStatus": this.type,
						"searchCondition": this.searchText,
						"pn": this.pn,
						"ps": this.ps
					}
				} else {
					obj = {
						"searchCondition": this.searchText,
						"pn": this.pn,
						"ps": this.ps
					}
				}
				const {
					code,
					data,
				} = await API.approveRecordList(obj);
				if (code == '0000') {
					this.boxTableData = data.list;
				}
			},
		}
	}
</script>
<style lang="less">
	.dataShow {
		height: 100%;

		.flex {
			display: -webkit-flex;
			/* Chrome 21+, Safari 6.1+, iOS Safari 7+, Opera 15/16 */
			display: -moz-flex;
			/* Firefox 18+ */
			display: -ms-flexbox;
			/* IE 10 */
			display: flex;
			/* Chrome 29+, Firefox 22+, IE 11+, Opera 12.1/17/18, Android 4.4+ */
		}

		.flex-wrap {
			-webkit-flex-wrap: wrap;
			-moz-flex-wrap: wrap;
			flex-wrap: wrap;
		}

		.flex-direction {
			flex-direction: row;
			-webkit-flex-direction: row;
		}

		.flex-direction-column {
			flex-direction: column;
			-webkit-flex-direction: column;
		}

		.justify-content {
			-webkit-justify-content: space-between;
			justify-content: space-between;
		}

		.justify-content-center {
			-webkit-justify-content: center;
			justify-content: center;
		}

		.align-items-center {
			align-items: center;
		}

		.align-items-end {
			align-items: flex-end;
		}

		.align-items-baseline {
			align-items: baseline;
		}

		.base-data .item {
			height: 2rem;
			margin: 10px 5px 5px 5px;
			background: #1A2833;
			border: 1px #48596D solid;
		}

		.grad {
			background: -webkit-linear-gradient(#29D9C2, #1E68E4);
			/* Safari 5.1 - 6.0 */
			background: -o-linear-gradient(#29D9C2, #1E68E4);
			/* Opera 11.1 - 12.0 */
			background: -moz-linear-gradient(#29D9C2, #1E68E4);
			/* Firefox 3.6 - 15 */
			background: linear-gradient(#29D9C2, #1E68E4);
			/* 标准的语法 */
		}

		.swiper-slide {
			height: auto;
		}
	}
</style>



